import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import ExperienceAccordion from "../../containers/experienceAccordion/ExperienceAccordion.js";
import "./Experience.css";
import { experience } from "../../portfolio.js";
import { Fade } from "react-reveal";
import portfolio_pic from "../../images/portfolio_pic.png";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import { greeting } from "../../portfolio";

class Experience extends Component {
    render() {
        const theme = this.props.theme;
        return (
            <div className="experience-main">
                <Header theme={theme} />
                <div className="basic-experience">
                    <Fade bottom duration={2000} distance="40px">
                        <div className="experience-heading-div">
                            <div className="experience-heading-img-div">
                                <img src={portfolio_pic} alt="the portfolio pice" class="porfolio_pic" />
                            </div>
                            <div className="experience-heading-text-div">
                                <h1
                                    className="experience-heading-text"
                                    style={{ color: theme.text }}
                                >
                                    {experience.title}
                                </h1>
                                <h3
                                    className="experience-heading-sub-text"
                                    style={{ color: theme.text }}
                                >
                                    {experience["subtitle"]}
                                </h3>
                                <p
                                    className="experience-header-detail-text subTitle"
                                    style={{ color: theme.secondaryText }}
                                >
                                    {experience["description"]}
                                </p>
                                <p  className="experience-header-detail-text subTitle"
                                    style={{ color: theme.secondaryText }}
                                >
                                    Feel free to expand any section to learn more about me! 
                                </p>
                                <div className="resume-btn-div">
                                    <Button
                                        text="Check out my resume"
                                        newTab={true}
                                        href={greeting.resumeLink}
                                        theme={theme}
                                        className="portfolio-repo-btn"
                                    />
                                </div>
                                <SocialMedia theme={theme} />
                            </div>
                        </div>
                    </Fade>
                </div>
                <ExperienceAccordion sections={experience["sections"]} theme={theme} />
                <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
            </div>
        );
    }
}

export default Experience;
