import React from "react";
import "./projectIntro.css";

import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import Button from "../../components/button/Button";
import ProjectLanguages from "../../components/projectLanguages/ProjectLanguages";

import { useParams, NavLink } from "react-router-dom";
import { Fade } from "react-reveal"; // Import the Fade component
import { projects } from "../../portfolio";


function ProjectIntro(props) {
    const { projectName } = useParams();
    const project = projects.find((proj) => proj.name === projectName);

    if (!project) {
        return <div>Project not found</div>;
    }

    const theme = props.theme;
    const { title, description, image, techStack, repoUrl, lastUpdate } = project;
    const img = require(`../../images/${image}`);
    const lines = description.split('\n');

    return (
        <div>
            <Header theme={theme} />

            <div className="container">
                <Fade duration={2000} distance="40px">
                    <div className="left">
                        <div className="project-meta-item">
                            <h3 className="tech-stack">Tech Stack</h3>
                            <ProjectLanguages
                                logos={techStack}
                            />
                        </div>
                        <div className="project-meta-item">
                            <h3>Links</h3>
                            {repoUrl ? (
                                repoUrl.map((link) => (
                                    <p key={link.name}>
                                        <a href={link.URL} target="_blank" rel="noopener noreferrer">
                                            {link.name}
                                        </a>
                                    </p>
                                ))
                            ) : (
                                <p> In Progress </p>
                            )}
                        </div>
                        <div className="project-meta-item">
                            <h3>Last Updated</h3>
                            <p>{lastUpdate}</p>
                        </div>
                    </div>

                    <div className="right">
                        <div className="project-content">
                            <div className="project-meta">

                            </div>
                            <div className="project-title">
                                <h1>{title}</h1>
                            </div>
                            <div className="project-description">
                                {lines.map((line, index) => (
                                    <React.Fragment key={index}>
                                        {line}
                                        <br /><br />
                                    </React.Fragment>
                                ))}
                            </div>
                            <div>
                                <img className="project-image" src={img} alt="DynamicImage" />
                            </div>
                        </div>
                    </div>

                </Fade>
            </div>
            <div className="btn-div">
                <Fade duration={2000} distance="40px">
                    <NavLink to="/projects" >
                        <Button
                            text="Back to projects"
                            theme={theme}
                        />
                    </NavLink>
                </Fade>
            </div>
            <Footer theme={theme} />
        </div>
    );
}

export default ProjectIntro;
